import React, { useState, useEffect } from 'react';

import { FiPower } from 'react-icons/fi';
import { IoIosCut } from 'react-icons/io';

import { Link, useHistory } from 'react-router-dom';
import * as S from './styles';

import logoImg from '../../assets/logo.svg';

import { useAuth } from '../../hooks/auth';
import api from '../../services/api';

import Swal from 'sweetalert2';

interface Shaver {
  id: string;
  name: string;
  status: number;
  avatar: string;
  avatar_url: string;
}

interface Catalog {
  id: string;
  description: string;
  price: number;
}

interface Order {
  shaver_id: string;
  catalog_id: string;
}

const Dashboard: React.FC = () => {
  const { user, signOut } = useAuth();
  const history = useHistory();

  const [shaver, setShaver] = useState<Shaver>();
  const [catalog, setCatalog] = useState<Catalog[]>([]);

  const getShaver = async () => {
    setShaver(JSON.parse(String(localStorage.getItem('@ShaveXP:shaver'))));
  }

  const getCatalogShaver = async () => {
    api
      .get<Catalog[]>('/catalog')
      .then(response => {
        setCatalog(response.data);
      })
  }

  useEffect(() => {
    getShaver()
    getCatalogShaver()
  }, []);

  const handleCatalog = (cat: Catalog) => {

    Swal.fire({
      title: cat.description,
      text: 'Deseja confirmar a solicitação do serviço?',
      showCloseButton: true,
      showDenyButton: true,
      focusConfirm: true,
      confirmButtonText: `<i class="fa fa-thumbs-up"></i> Confirmar`,
      confirmButtonAriaLabel: 'accept',
      denyButtonText: '<i class="fa fa-thumbs-down"></i> Cancelar',
      denyButtonAriaLabel: 'Thumbs down',
    }).then((result) => {
      if (result.isConfirmed) {
        if (shaver) {
          api
          .post<Order>('/orders',  { shaver_id: shaver.id, catalog_id: cat.id })
          .then(async response => {
            localStorage.removeItem('@ShaveXP:shaver');
            history.push('/order');
          }).catch(error => {
            Swal.fire({
              title: 'Oops!',
              text: 'Ocorreu um erro estranho ao solicitar o serviço. Veja mais detalhes em DevTools do navegador.'
            })
            console.log(error)
          })
        }
      } 
    })
  }

  return (
    <S.Container>
      <S.Header>
        <S.HeaderContent>
          <img src={logoImg} alt="Logo Shave eXperencie" onClick={() => history.push('/')} />

          <S.HeaderProfile className="logged-user">
            <img
              src={`https://avatars.dicebear.com/api/initials/${user?.name}.svg`}
              alt={user.name}
            />

            <div>
              <Link to="#">
                <span>Olá,</span> <strong>{user.name.split(' ')[0]}</strong>
              </Link>
              <button type="button" className="btn-signout" onClick={signOut}>
                Sair<FiPower color="#ed565e" /> 
              </button>
            </div>
          </S.HeaderProfile>
        </S.HeaderContent>
      </S.Header>

      <S.Content>
        <S.Work>

          <div className="subtitle"><h1>Escolha o serviço</h1></div>

          <S.Section>

          {shaver && (
            <S.Item>
              <figure key={shaver.id}>
              <img
                  src={shaver.avatar_url}
                  alt={shaver.avatar}
                />
                <figcaption>
                  <div className="content">
                    <h3>{shaver.name}</h3>
                    <p>
                      Será o seu Shaver
                    </p>
                  </div>
                </figcaption>
              </figure>
            </S.Item>
          )}

          <S.Catalog className="catalog">
            {catalog.map(cat => (
              <div key={cat.id} className="catalog-item"  onClick={() => handleCatalog(cat)}>
                <h3><IoIosCut size={25} /> {cat.description}</h3>
                <small>{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(cat.price)}</small>
              </div>
            ))}
          </S.Catalog>

          </S.Section>
        </S.Work>
      </S.Content>
    </S.Container>
  );
};

export default Dashboard;
