import React, { useCallback, useRef, useState } from 'react';
import { FiArrowLeft, FiMail, FiUser, FiLock } from 'react-icons/fi';
import { FormHandles } from '@unform/core';
import { Form } from '@unform/web';
import * as Yup from 'yup';
import { Link } from 'react-router-dom';

import api from '../../services/api';
import getValidationErrors from '../../utils/getValidationErrors';

import Input from '../../components/Input';
import Button from '../../components/Button';
import Notice, { NoticeProps } from '../../components/Notice';

import { Container, Content, AnimationContainer, Background } from './styles';

import logoImg from '../../assets/logo.svg';

interface SignUpFormData {
  name: string;
  email: string;
  password: string;
}

const SignUp: React.FC = () => {
  const formRef = useRef<FormHandles>(null);
  // const history = useHistory();

  const [notice, setNotice] = useState<NoticeProps>({
    display: false
  });

  const handleSubmit = useCallback(
    async (data: SignUpFormData) => {
      try {
        formRef.current?.setErrors({});

        const schema = Yup.object().shape({
          name: Yup.string().required('Nome é obrigatório'),
          email: Yup.string()
            .required('E-mail é obrigatório')
            .email('Informe um email válido'),
          password: Yup.string().trim().transform(value => value === '' ? undefined : value)
            .required('Senha é obrigatória')
            .min(6, 'Pelo menos 6 caracteres')
        });

        await schema.validate(data, { abortEarly: false });

        api.post('/users', data)
          .then((response) => {
            setNotice({
              display: true,
              type: 'success',
              message: 'Boas vindas, faça login para solicitar serviços!'
            })
          })
          .catch((err) => {
            if (err.response.data && err.response.data.message === 'Email address already used.') {

              console.log({ mode: 'Dev', err })

              setNotice({
                display: true,
                type: 'error',
                message: 'Oops! E-mail já cadastrado.'
              })

            }
          })


      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err);

          formRef.current?.setErrors(errors);
          return;
        }

        setNotice({
          display: true,
          type: 'error',
          message: 'Ocorreu um erro ao fazer cadastro, tente novamente.'
        })
      }
    },
    [],
  );

  return (
    <Container>
      <Content>
        <AnimationContainer>
          <img src={logoImg} alt="Shave eXperience" className="logo" />


          <Form ref={formRef} onSubmit={handleSubmit} >
            <h1>Faça seu cadastro</h1>

            <Input name="name" icon={FiUser} placeholder="Nome completo" />

            <Input name="email" icon={FiMail} placeholder="Seu melhor email" />

            <Input
              name="password"
              icon={FiLock}
              type="password"
              placeholder="Sua senha secreta"
            />

            <Notice display={notice.display} message={notice.message} type={notice.type} />

            <Button type="submit">Cadastrar</Button>
          </Form>


          <Link to="/">
            <FiArrowLeft />
            Voltar para login
          </Link>
        </AnimationContainer>
      </Content>
      <Background />
    </Container>
  );
};

export default SignUp;
