import styled from 'styled-components';
export const Container = styled.div``;

export const Header = styled.header`
  padding: 32px 0;
  background: #1e1c23;
`;

export const HeaderContent = styled.div`
  max-width: 1120px;
  margin: 0 auto;
  display: flex;
  align-items: center;

  > img {
    height: 120px;
    cursor: pointer;
  }
`;

export const HeaderProfile = styled.div`
  display: flex;
  align-items: center;
  margin-left: auto;

  img {
    width: 56px;
    height: 56px;
    border-radius: 50%;
  }

  div {
    display: flex;
    flex-direction: column;
    margin-left: 16px;
    line-height: 24px;

    span {
      color: #ffc885;
    }

    a {
      text-decoration: none;
      color: #ffc885;

      transition: opacity 0.3s;
      &:hover {
        opacity: 0.8;
      }
    }

    .btn-signout {
      display: flex;
      align-items: center;
      margin-left: auto;
      color: #f4ede8;
      background: none;
      border: none;
      font-size: 0.8rem;
  
      svg {
        color: #D81B60;
        margin-left: 5px;
      }
    }

  }
`;

export const Content = styled.main`
  max-width: 1120px;
  margin: 34px auto;
  display: flex;
`;

export const Work = styled.div`
  flex: 1;
  
  h1 {
    font-size: 36px;
    margin-left: 15px;
  }
`;

export const Section = styled.section`
  margin-top: 20px;

  @media (max-width: 1280px)
  {
    .not-found {
      text-align: center;
  
      img {
        width: 720px;
      }
    }
  }

  > strong {
    color: #999591;
    font-size: 20px;
    line-height: 26px;
    border-bottom: 1px solid #1e1c23;
    display: block;
    padding-bottom: 16px;
    margin-bottom: 16px;
  }

  > p {
    color: #999591;
  }
`;

export const Item = styled.div`
  display: grid;  
  margin-top: 1px;
  grid-template-columns: 1fr;

  figure {
    display: grid;  
    grid-template-columns: 250px 1fr;
    background: #1e1c23;
    border-radius: 25px;
    padding: 30px;
    gap: 50px;
  }

  figcaption {
    display: table;
  }

  figcaption .content {
    display: table-cell;
    vertical-align: middle;
  }

  figcaption .content h3 {
    margin: 0;
    font-size: 3.8em;
    font-weight: bold;
  }

  figcaption .content p {
    display: flex;
    color: #ccc;
    align-items: center;
    font-size: 1.8em;

    svg {
      color: #ffc885;
      margin-right: 5px;
    } 
  }

  figure img {
    width: 100%;
  }

`

export const Catalog = styled.div`
  display: flex;  
  margin-top: 40px;
  grid-template-columns: auto;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: flex-start;

  .catalog-item:hover {
    border: 2px solid #ffc885;
  }

  .catalog-item {
    cursor: pointer;
    background: #1e1c23;
    border: 2px solid #1e1c23;
    border-radius: 25px;
    padding: 20px 50px 20px 50px;
    font-size: 1.5em;
  }

  .catalog-item h3 {
    display: flex;
    color: #ccc;
    align-items: center;

    svg {
      color: #ffc885;
      margin-right: 5px;
    } 
  }

  .catalog-item small {
    margin-left: 2px;
    color: #16A085;
  }

`