import { createGlobalStyle } from 'styled-components';

export default createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
  }

  body {
    background: #1a181e;
    color: #f4ede8;
    -webkit-font-smoothing: antialiased;
  }

  body, input, button {
    font-family: 'Roboto Slab', serif;
    font-size: 16px;
  }

  .form-input {
    margin-top: 8px;
  }

  .alert {
    text-align: left;
    margin: 0px 0px 10px 10px !important;
  }

  .alert-error {
    color: #c53030 !important;
  }

  h1, h2, h3, h4, h5, h6, strong {
    font-weight: 500;
  }

  button {
    cursor: pointer;
  }

  .logo {
    width: 322px;
  }

  .brand {
    margin-top: 35px;
    width: 120px;
  }

  .profile .notice {
    margin-top: 0 !important;
    margin-bottom: 1.5rem;
  }

  .swal2-popup {
    background: #1e1c23 !important;
    color: #545454;
  }

  .swal2-styled.swal2-confirm {
    background-color: #ffc885 !important;
    color: #1a181e !important;
  }

  .swal2-styled.swal2-confirm:focus {
    box-shadow: 0 0 0 3px rgb(255 200 133 / 50%) !important;
  }

  .swal2-styled.swal2-deny {
    background-color: #6e7881 !important;
  }

  .swal2-styled.swal2-deny:focus {
    box-shadow: 0 0 0 3px rgb(110 120 129 / 50%);
  }
  
  #swal2-title {
    color: #f4ede8 !important;
  }

  #swal2-html-container {
    color: #ccc !important;
  }

  /* input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active  {
    box-shadow: 0 0 0px 1000px #1e1c23 inset;
    transition: "color 9999s ease-out, background-color 9999s ease-out";
    transition-delay: 9999s;
  } */  
`;
