import React, { useRef, useCallback, useState } from 'react';
import { FiLock, FiLogIn } from 'react-icons/fi';
import { FormHandles } from '@unform/core';
import { Form } from '@unform/web';
import * as Yup from 'yup';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router-dom';

import getValidationErrors from '../../utils/getValidationErrors';

import Input from '../../components/Input';
import Button from '../../components/Button';
import Notice, { NoticeProps } from '../../components/Notice';

import { Container, Content, AnimationContainer, Background } from './styles';

import logoImg from '../../assets/logo.svg';
import api from '../../services/api';

interface ResetPasswordFormData {
  password: string;
  password_confirmation: string;
}

const ResetPassword: React.FC = () => {
  const formRef = useRef<FormHandles>(null);
  const location = useLocation();

  const [notice, setNotice] = useState<NoticeProps>({
    display: false
  });

  const handleSubmit = useCallback(
    async (data: ResetPasswordFormData) => {
      try {
        formRef.current?.setErrors({});

        const schema = Yup.object().shape({
          password: Yup.string().required('Senha é obrigatória'),
          password_confirmation: Yup.string().oneOf(
            [Yup.ref('password'), null],
            'Confirmação incorreta',
          ),
        });

        await schema.validate(data, { abortEarly: false });

        const { password, password_confirmation } = data;
        const token = new URLSearchParams(location.search).get('token');

        if (!token) {
          throw new Error();
        }

        await api.post('/password/reset', {
          password,
          password_confirmation,
          token,
        }).then((res)=>{
          setNotice({
            display: true,
            type: 'success',
            message: 'Agora você já pode logar com a sua nova senha secreta.',
          })
        }).catch(error => {
          if (error.response.data && error.response.data.message === 'Token expired') {
            setNotice({
              display: true,
              type: 'info',
              message: 'Tempo egotado. Por favor, faça outra solicitação de recuperação de senha.',
            })
          }

          if (error.response.data && error.response.data.message === 'User token does not exists') {
            setNotice({
              display: true,
              type: 'error',
              message: 'Por favor, faça uma nova solicitação de recuperação de senha.',
            })
          }
        })

      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err);

          formRef.current?.setErrors(errors);
          return;
        }

        console.log({ mode: 'Dev', err })

        setNotice({
          display: true,
          type: 'error',
          message: 'Ocorreu um erro ao resetar sua senha, tente novamente.'
        })
      }
    },
    [location.search],
  );

  return (
    <Container>
      <Content>
        <AnimationContainer>
          <img src={logoImg} alt="Shave eXperencie" className="logo" />

          <Form ref={formRef} onSubmit={handleSubmit}>
            <h1>Resetar senha</h1>

            <Input
              name="password"
              icon={FiLock}
              type="password"
              placeholder="Nova senha"
            />

            <Input
              name="password_confirmation"
              icon={FiLock}
              type="password"
              placeholder="Confirmação da senha"
            />

            <Button type="submit">Alterar senha</Button>

            <Notice display={notice.display} message={notice.message} type={notice.type}/>

          </Form>

          <Link to="/">
            <FiLogIn />
            Voltar ao login
          </Link>
        </AnimationContainer>
      </Content>
      <Background />
    </Container>
  );
};

export default ResetPassword;
