import React, { useState, useEffect } from 'react';

import { FiPower, FiAward } from 'react-icons/fi';

import { Link, useHistory } from 'react-router-dom';
import * as S from './styles';

import logoImg from '../../assets/logo.svg';

import { useAuth } from '../../hooks/auth';
import api from '../../services/api';

import Swal from 'sweetalert2';

interface Shaver {
  id: string;
  name: string;
  status: number;
  avatar: string;
  avatar_url: string;
  level: string;
}

interface Order {
  id: string;
  status: number;
  shaver_id: string;
}

const Dashboard: React.FC = () => {
  const { user, signOut } = useAuth();
  const history = useHistory();

  const [shavers, setShavers] = useState<Shaver[]>([]);

  const getShavers = async () => {
    api
      .get<Shaver[]>('/shavers')
      .then(response => {
        setShavers(response.data);
      });
  }

  useEffect(() => {
    getShavers()
  }, []);

  const handleOpen = (shaver: Shaver) => {
    api
      .get<Order[]>(`/orders/${shaver.id}/available`)
      .then(response => {
        if (response.data.length > 0) {
          return Swal.fire({
            title: 'Oops!',
            text: `${shaver.name} está em atendimento!`
          })
        } else {
          localStorage.setItem('@ShaveXP:shaver', JSON.stringify(shaver));
          history.push('/catalog');
        }
      });
  }

  return (
    <S.Container>
      <S.Header>
        <S.HeaderContent>
          <img src={logoImg} alt="Logo Shave eXperencie" />

          <S.HeaderProfile className="logged-user">
            <img
              src={`https://avatars.dicebear.com/api/initials/${user?.name}.svg`}
              alt={user.name}
            />

            <div>
              <Link to="#">
                <span>Olá,</span> <strong>{user.name.split(' ')[0]}</strong>
              </Link>
              <button type="button" className="btn-signout" onClick={signOut}>
                Sair<FiPower color="#ed565e" /> 
              </button>
            </div>
          </S.HeaderProfile>
        </S.HeaderContent>
      </S.Header>

      <S.Content>
        <S.Work>
          {shavers.length > 0 && (
            <div className="subtitle"><h1>Shavers Disponíveis</h1></div>
          )}
          <S.Section>

            <S.List>
            {shavers.map(shaver => (
                <figure key={shaver.id} onClick={() => handleOpen(shaver)}>
                  <figcaption>
                    <div className="content">
                      <h3>{shaver.name}</h3>
                      <small>
                        <FiAward size={15} /> {shaver.level}
                      </small>
                    </div>
                  </figcaption>
                  <img
                    src={shaver.avatar_url}
                    alt={shaver.avatar}
                  />
                </figure>
              ))}
              </S.List>


          </S.Section>
        </S.Work>
      </S.Content>
    </S.Container>
  );
};

export default Dashboard;
